.bf-checkbox-label:hover,
.bf-checkbox-label:hover .bf-checkbox-icon {
  color: var(--bfc-theme-2);
}

.bf-checkbox-label,
.bf-content .bf-checkbox-label {
  display: inline-block;
  padding: var(--bfs2) var(--bfs4) var(--bfs2) var(--bfs24);
  cursor: pointer;
  position: relative;
}
.bf-checkbox-label:not(.bf-button) {
  border: 1px dashed transparent;
  border-radius: 3px;
}
.bf-checkbox-icon {
  vertical-align: middle;
  color: var(--bff-theme);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}
.bf-checkbox-wrapper {
  font-size: var(--bf-font-size-m);
  --bf-switch-height: var(--bfs24);
  --bf-switch-width: 44px;
  --bf-switch-thumb-size: calc(var(--bf-switch-height) - 4px);
}
.bf-checkbox-wrapper .bf-checkbox {
  /* you cannot focus on a field with
   * visibility: hidden; or display: none;
	 * so we hide it some other way */
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.bf-checkbox-label-text {
  font-weight: normal;
}
/* focus state */
.bf-checkbox[data-focus-visible-added] ~ .bf-checkbox-label:not(.bf-button) {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}

/* checkbox align */
.bf-checkbox-right .bf-checkbox-label {
  display: block;
  padding-left: 0;
  padding-right: var(--bfs24);
}
.bf-checkbox-right .bf-checkbox-icon {
  left: auto;
  right: 0;
}

/* button style */
.bf-checkbox-label.bf-button {
  display: block;
  padding: var(--bfs8) var(--bfs16) var(--bfs8) 34px;
  max-width: none;
  border-color: var(--bff-dimmed);
  color: var(--bfc-base-c-1);
}
.bf-checkbox-label.bf-button .bf-checkbox-icon {
  left: var(--bfs12);
}
.bf-checkbox[data-focus-visible-added] ~ .bf-button {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}
.bf-checkbox-label.bf-button:hover {
  background-color: var(--bfc-base-3);
  color: var(--bfc-theme-2);
}
/* button align */
.bf-checkbox-right .bf-checkbox-label.bf-button {
  padding: var(--bfs8) 34px var(--bfs8) var(--bfs16);
}
.bf-checkbox-right .bf-checkbox-label.bf-button .bf-checkbox-icon {
  left: auto;
  right: var(--bfs12);
}

/* switch styles */
.bf-switch-wrapper .bf-checkbox-label {
  padding: var(--bfs8) 0;
}
.bf-switch {
  height: var(--bf-switch-height);
  width: var(--bf-switch-width);
  background-color: var(--bfc-base-c-2);
  border-radius: 50px;
  position: absolute;
  right: 0;
  padding: 2px;
}
.bf-checkbox-left .bf-switch {
  right: auto;
  left: 0;
}
.bf-checkbox-right .bf-button .bf-switch {
  right: 12px;
}
.bf-checkbox-left .bf-button .bf-switch {
  right: auto;
  left: 12px;
}
.bf-checkbox-left.bf-switch-wrapper .bf-checkbox-label {
  padding-left: var(--bfs52);
}
.bf-checkbox-right.bf-switch-wrapper .bf-checkbox-label {
  padding-right: var(--bfs52);
}
.bf-checkbox-left.bf-switch-wrapper .bf-checkbox-label.bf-button {
  padding-left: 68px;
}
.bf-checkbox-right.bf-switch-wrapper .bf-checkbox-label.bf-button {
  padding-right: 68px;
}

.bf-switch-thumb {
  width: var(--bf-switch-thumb-size);
  line-height: var(--bf-switch-thumb-size);
  text-align: center;
  position: absolute;
  border-radius: 50px;
  background-color: var(--bfc-base-3);
  left: 2px;
  /* transition short-hand does not work with multiple properties */
  transition-property: background-color, margin-left;
  transition-duration: var(--bf-animation-duration);
  transition-timing-function: var(--bf-timing-function);
  font-size: 11px; /* magic value appears to almost fix centering browser rendering issue */
  color: var(--bfc-base-c-2);
}
label:hover .bf-switch {
  background-color: var(--bfc-base-c-inverted-2);
}

/* switched on */
.bf-checkbox:checked ~ label .bf-switch {
  background-color: var(--bfc-theme);
}
.bf-checkbox:checked ~ label:hover .bf-switch {
  background-color: var(--bfc-theme-2);
}
.bf-checkbox:checked ~ label .bf-switch-thumb {
  margin-left: calc(var(--bf-switch-width) - var(--bf-switch-height));
  background-color: var(--bfc-theme-c);
  color: var(--bfc-theme);
}
.bf-checkbox:checked ~ label:hover .bf-switch-thumb {
  color: var(--bfc-theme-2);
}

/* indeterminate state */
.bf-checkbox-indeterminate .bf-checkbox-icon:after {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  top: 50%;
  left: 50%;
  background-color: currentColor;
  transform: translate(-50%, -50%);
}

/* hidden label triggers inline mode */
.bf-checkbox-hidelabel .bf-checkbox-label-text {
  visibility: hidden;
  width: 0;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
}
.bf-checkbox-hidelabel.bf-checkbox-wrapper {
  display: inline-block;
}
.bf-checkbox-hidelabel.bf-checkbox-wrapper .bf-checkbox-label {
  padding: 0 0 0 18px;
  display: inline;
}
.bf-checkbox-hidelabel.bf-switch-wrapper .bf-checkbox-label {
  padding: 6px 0 0 var(--bf-switch-width);
  display: inline;
  border-radius: 20px;
}

/* disabled */
.bf-checkbox:disabled ~ label,
.bf-checkbox:disabled ~ label:hover,
.bf-checkbox:disabled ~ label .bf-checkbox-icon {
  color: var(--bff-disabled);
  cursor: default;
}
.bf-checkbox:disabled ~ .bf-button,
.bf-checkbox:disabled:checked ~ .bf-button {
  border-color: var(--bff-dimmed);
  background-color: var(--bfc-base-3);
  color: var(--bff-disabled);
}
.bf-checkbox:disabled ~ label .bf-switch,
.bf-checkbox:disabled ~ label:hover .bf-switch {
  background-color: var(--bff-disabled);
}
.bf-checkbox:disabled ~ label .bf-switch-thumb {
  background-color: var(--bfc-base-3);
  color: var(--bfc-base-c-wcag);
}
